.fa-times {
  transition: all .3s ease;
  color: rgba(175, 47, 47, 0.15);
}

.mark {
  position: absolute;
  left: 20px;
}

.todo-item:hover .fa-times {
  color: rgba(175, 47, 47, 0.9);
}
.todo-list {
    margin: 0;
    padding: 0;
  }
.footer {
  position: relative;
  min-height: 50px;
  display: flex;
  align-items: center;
      padding: 0 6px 0 21px;
  justify-content: space-between;
}

.amount {
  font-size: 13.3333px;
}

.btn-group {
  position: relative;
  z-index: 1;
}

.filter-btn {
  margin: 3px;
  padding: 3px 7px;
  border: 1px solid transparent;
  border-radius: 3px;
  background-color: transparent;
  cursor: pointer;
}

.filter-btn:hover,
.filter-btn.active {
  border-color: rgba(175, 47, 47, 0.2);
}

.footer:before {
    content: '';
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    height: 50px;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2),
                0 8px 0 -3px #f6f6f6,
                0 9px 1px -3px rgba(0, 0, 0, 0.2),
                0 16px 0 -6px #f6f6f6,
                0 17px 2px -6px rgba(0, 0, 0, 0.2);
}
.todo-wrapper {
    max-width: 600px;
    margin: 0 auto;
    position: relative;
    background-color: rgb(227, 239, 255);
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2),
                0 25px 50px 0 rgba(0, 0, 0, 0.1);
  }
.title {
  font-family: 'Permanent Marker', cursive;
  font-size: 80px;
  text-align: center;
  font-weight: 900;
  color: rgba(98, 95, 247, 0.15);
  margin-bottom: 20px;
}
